<template>
    <div>
        <h5>Conut 组件</h5>
        <p>count的值是：{{ count }}</p>
        <el-button @click="add">+1</el-button>
    </div>
</template>

<script>

export default {
    data(){
        return {
            count: this.init
        }
    },
    methods: {
        add(){
            this.count++
        }
    },

    // props: ['init']
    props: {
        init: {
            default: 0,
            typeof: Number,
            //必填项校验
            required: true
        }
    }
}
</script>

